import React, { useState, useEffect } from 'react';

const AttendanceForm = () => {
  const [sheetNames, setSheetNames] = useState([]);
  const [selectedSheet, setSelectedSheet] = useState('');
  const [date, setDate] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');
  const [scriptURL, setScriptURL] = useState('');

  useEffect(() => {
    // Fetch the script URL from backend
    fetch("https://presence-pro-backend.vercel.app/api/uploadurl/fetchurl", {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        "auth-token": localStorage.getItem("authToken"),
      },
    })
      .then(response => response.json())
      .then(data => {
        if (data.length > 0) {
          setScriptURL(data[0].url); // Assuming the first URL is the correct one
        }
      })
      .catch(error => console.error("Error fetching script URL:", error));
  }, []);

  useEffect(() => {
    if (!scriptURL) return;
    setIsLoading(true);
    
    fetch(`${scriptURL}?action=getSheets`)
      .then(response => response.json())
      .then(data => {
        setSheetNames(data.sheetNames);
        setIsLoading(false);
      })
      .catch(error => {
        setErrorMessage('Error loading sheet names!');
        setIsLoading(false);
      });
  }, [scriptURL]);

  const handleSubmit = (e) => {
    e.preventDefault();
    setIsSubmitting(true);
    
    if (!scriptURL) {
      alert("No script URL available!");
      setIsSubmitting(false);
      return;
    }

    const formData = new FormData();
    formData.append('sheetName', selectedSheet);
    formData.append('newDate', date);

    fetch(scriptURL, { method: 'POST', body: formData })
      .then(response => response.json())
      .then(result => {
        if (result.result === 'success') {
          alert('Success: ' + (result.message || 'Attendance marked successfully!'));
        } else {
          alert('Error: ' + result.error);
        }
        setIsSubmitting(false);
      })
      .catch(error => {
        alert('Error submitting the form!');
        setIsSubmitting(false);
      });
  };

  return (
    <div className="relative flex flex-col w-full max-w-lg mx-auto text-gray-700 bg-[#2D3748] shadow-lg rounded-xl bg-clip-border  ">
      <h2 className="text-2xl pt-4 font-semibold text-white text-center">
        Mark Attendance On Google Sheet
      </h2>
      {isLoading && <p className="text-center text-white">Loading sheet names...</p>}
      {errorMessage && <p className="text-center text-red-500">{errorMessage}</p>}
      <form onSubmit={handleSubmit} className="grid grid-cols-1 gap-4 p-6">
        <div className="relative">
          <select
            value={selectedSheet}
            onChange={(e) => setSelectedSheet(e.target.value)}
            className="w-full px-4 py-3 text-sm font-normal text-gray-700 transition-all bg-gray-100 border rounded-lg shadow-sm focus:ring-2 focus:ring-blue-500 focus:outline-none"
          >
            <option value="">Select Subject</option>
            {sheetNames.length === 0 && !isLoading && (
              <option disabled>No sheets available</option>
            )}
            {sheetNames.map((sheetName, index) => (
              <option key={index} value={sheetName}>
                {sheetName}
              </option>
            ))}
          </select>
        </div>

        <div className="relative">
          <input
            type="date"
            value={date}
            onChange={(e) => setDate(e.target.value)}
            className="w-full px-4 py-3 text-sm font-normal text-gray-700 transition-all bg-gray-100 border rounded-lg shadow-sm focus:ring-2 focus:ring-blue-500 focus:outline-none"
          />
        </div>

        <div className="pb-2">
          <button
            className="w-full px-6 py-3 text-sm font-bold text-white uppercase bg-blue-600 rounded-lg shadow-md hover:bg-blue-700 focus:ring-2 focus:ring-blue-400 transition-all"
            type="submit"
            disabled={isSubmitting}
          >
            {isSubmitting ? 'Submitting...' : 'Mark'}
          </button>
        </div>
      </form>
    </div>
  );
};

export default AttendanceForm;
