import './App.css';
import React, { useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, useLocation } from 'react-router-dom'; // Import Router components
import Sidebar from './components/Teacher/Sidebar';
import About from './components/static/About';
import Home from './components/Teacher/Home';
import Contact from './components/static/Contact';
import Class from './components/Teacher/Class';
import Microproject from './components/Teacher/Microproject';
import Microlist from './components/Teacher/Microlist';
import Calendar from './components/static/Calender';
import Signin from './components/Teacher/Signin';
import Event from './components/static/Event';
import Attendance from './components/Teacher/Attendance';
import StdHome from './components/student/StdHome';
import StdClass from './components/student/StdClass'
import StdMicroprIteam from './components/student/StdMicroprIteam';
import StdAttendance from './components/student/StdAttendance'
import AddMicroproject from './components/student/AddMicroproject';
import { AppContextProvider } from './context/AppContextProvider';
import "react-toastify/dist/ReactToastify.css";
import ClassUpload from './components/Teacher/ClassUpload';
import StdGetClass from './components/student/StdGetClass';
import GoogleSheetGuide from './components/static/GoogleSheetGuide';


function App() {
  useEffect(() => {
    const hideScrollbarStyles = `
      html, body {
        overflow-y: scroll;
      }
      html::-webkit-scrollbar {
        display: none;
      }
      body::-webkit-scrollbar {
        display: none;
      }
      body {
        -ms-overflow-style: none; /* For IE and Edge */
        scrollbar-width: none;   /* For Firefox */
      }
    `;

    const style = document.createElement('style');
    style.textContent = hideScrollbarStyles;
    document.head.appendChild(style);

    return () => {
      document.head.removeChild(style);
    };
  }, []);

  return (
    <Router>
      <MainContent />
    </Router>
  );
}

function MainContent() {
  const location = useLocation();

  // Define the routes where Sidebar should appear
  const sidebarRoutes = [
    '/home',
    '/new',
    '/about',
    '/stdhome',
    '/stdclass',
    '/contact',
    '/class',
    '/microproject',
    '/stdmicroproject',
    '/microlist',
    '/calendar',
    '/attendance',
    '/Dashboard',
    '/classnote',
    '/stdmicroiteam',
    '/stdattendance',
    '/classupload',
    '/stdgetclass',
    '/googleSheet'
  ];

  return (
    <AppContextProvider>
      {sidebarRoutes.includes(location.pathname) && <Sidebar />}
      <div>
        <Routes>
          <Route exact path="/" element={<Signin />} />
          <Route exact path="/home" element={<Home />} />
          <Route exact path="/stdhome" element={<StdHome />} />
          <Route exact path="/Dashboard" element={<Home />} />
          <Route exact path="/new" element={<Event />} />
          <Route exact path="/about" element={<About />} />
          <Route exact path="/contact" element={<Contact />} />
          <Route exact path="/class" element={<Class />} />
          <Route exact path="/stdclass" element={<StdClass />} />
          <Route exact path="/microproject" element={<Microproject />} />
          <Route exact path="/stdmicroproject" element={<StdMicroprIteam />} />
          <Route exact path="/stdmicroiteam" element={<AddMicroproject />} />
          <Route exact path="/microlist" element={<Microlist />} />
          <Route exact path="/calendar" element={<Calendar />} />
          <Route exact path="/attendance" element={<Attendance />} />
          <Route exact path="/stdattendance" element={<StdAttendance />} />
          <Route exact path="/googleSheet" element={<GoogleSheetGuide />} />
          <Route exact path="/classupload" element={<ClassUpload />} />
          <Route exact path="/stdgetclass" element={<StdGetClass />} />
        </Routes>
      </div>
    </AppContextProvider>
  );
}

export default App;




